.is-invalid.custom-toggle--checkbox .masa-modal__toggle-icon,
.custom-toggle--radio .masa-modal__toggle-icon {
  background: rgba(255, 94, 102, 0.2);
  border: 1px solid #ff5e66;
  opacity: 0.7;
}

.masa-modal__button {
  width: 194px;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .masa-modal__button {
    width: 100%;
  }
}

.masa-modal__select-text {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #484848;
}

